body {
  color: $primary-text-color;
  background-color: $primary-background-color;
  font-family: 'DM Sans', sans-serif;
}

img {
  display: block;
  width: 100%;
  height: auto;
}

.list {
  list-style: none;
  margin: 0;
  padding: 0;
}

.link {
  text-decoration: none;
  margin: 0;
  padding: 0;
}

.container {
  margin: 0 auto;
  padding: 0 20px;

  @media screen and (min-width: 320px) {
    width: 320px;
  }

  @media screen and (min-width: 768px) {
    width: 768px;
    padding: 0 32px;
  }

    @media screen and (min-width: 1200px) {
        width: 1200px;
        padding: 0 16px;
    }
  }

.section {
  padding: 60px 0;
}

.section--top-padding { 
    padding: 40px 0 60px 0;
    @media screen and (min-width: 768px) and (max-width: 1199px) {
        padding: 0 0 60px 0;
    }
    @media screen and (min-width: 1200px) {
        padding: 40px 0 120px 0;
    }
}

h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0;
  padding: 0;
}

.visually-hidden {
  position: absolute;
  white-space: nowrap;
  width: 1px;
  height: 1px;
  overflow: hidden;
  border: 0;
  padding: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(50%);
  margin: -1px;
}
